import Vue from 'vue'
import Router from 'vue-router'
//아래에 연결할 서브페이지(콤퍼넌드)를 import
import MainContent from '../components/MainContent.vue';
import Sub1Content from '../components/Sub1Content.vue';

import DetailContent from '../components/DetailContent.vue';

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    { path: '/vue/', component: MainContent},
    { path: '/vue/main', component: MainContent},
    { path: '/vue/sub1', component: Sub1Content},
    { path: '/vue/detail', component: DetailContent}
    // { path: '/', component: MainContent},
    // { path: '/main', component: MainContent},
    // { path: '/sub1', component: Sub1Content},
    // { path: '/detail', component: DetailContent}
  ]
})
