<template>
  <div class="tem">
    <div>
      <div class="box">
        <div class="search">
          <h3>관심있는 멸종위기 야생생물을 찾아보세요.</h3>
          <label class="hidden" for="title">생물검색 </label>
          <div class="valueBox" ref="valueBox">
            <input id="title" placeholder="동물이름이나 학명을 넣고 ENTER를 누르세요" type="text" v-model="value" @keyup.enter="search" />
            <span><button @click="search()">검색</button></span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// In the search component's script
import jdata from "../species.json";
export default {
  props: ["species"],
  data() {
    return {
      message: false,
      msgtext: "검색 데이터가 없습니다",
      value: "",
    };
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.handleScroll);
  },

  methods: {
    handleScroll() {
      const valueBox = this.$refs.valueBox;
      const scrollY = window.scrollY;
      console.log(scrollY)
      if (scrollY > valueBox.offsetTop) {
        valueBox.style.position = "fixed";
        valueBox.style.zIndex = "10";
        valueBox.style.top = "0";
        valueBox.style.left = "0";
        valueBox.style.width = "100%";
        valueBox.style.justifyContent = "center";

        


      } else {
        valueBox.style.position = "static";
      }
    },
    search() {
      let keyword = this.value;
      if (keyword) {
        let results = jdata.filter((element) => element.대표국명.includes(keyword) || element.학명.includes(keyword));
        this.message = true;
        this.msgtext = results.length == 0 ? "검색 데이터가 없습니다" : results.length + "개가 검색되었습니다";
        this.$emit("search-results", results);
        this.$emit("search-results-length", results.length);
      } else {
        alert("검색어를 입력해주세요!");
      }
    },
  },
};
</script>

