      }
      if (text2) {
        results = results.filter((element) => element.국가적색목록.includes(text2) || element.세계자연보전연맹.includes(text2));
      }
      if (text3) {
        results = results.filter((element) => element.분류군.includes(text3));
      }
      if (text4) {
        results = results.filter((element) => element.고유종.includes(text4));
      }
