<!-- 메인 페이지 마크업 -->
<template lang="html">
  <div class="box1">
    <div>
      <Search @search-results-length="handleSearchResultsLength" v-bind:species="species" v-on:search-results="handleSearchResults"></Search>
    </div>
    <div>
      <Group @search-results-length="handleSearchResultsLength" v-bind:species="species" v-on:search-results="handleSearchResults"></Group>
    </div>
    <div class="result">
      <p>현재 리스트 수: {{ searchResultsLength }} 건</p>
    </div>

    <div class="speciesList">
      <div v-for="x in species" :key="x.id" class="speciesBox">
        <router-link :to="{ path: '/vue/detail', query: { index: x.NO } }">
        <!-- <router-link :to="{ path: '/detail', query: { index: x.NO } }"> -->
          <img :src="require('@/assets/species/' + x.이미지)" alt="" />
          <div class="nameBox">
            <div class="name">
              <p>
                {{ x.대표국명 }}
              </p>
              <span>{{ "(" + x.Familyko + ")" }}</span>
            </div>
            <p class="hak">{{ x.학명 }}</p>
            <p class="hak2">{{ "멸종위기 " + x.등급2 + " 급 " + x.분류군 }}</p>
          </div>
        </router-link>
      </div>
      <GoTopBtn />
    </div>
  </div>
</template>

<script>
import jdata from "../species.json";
import Search from "./TextSearch.vue";
import Group from "./ClassGroup.vue";
import GoTopBtn from "./GoTopBtn.vue";

export default {
  components: {
    Search,
    Group,
    GoTopBtn,
  },
  data() {
    return {
      searchResultsLength: 267,
      message: false,
      msgtext: "검색 데이터가 없습니다",
      value: "",
      species: jdata,
      hovered: null,
      groups: ["포유류", "조류", "파충류", "양서류", "어류", "곤충류", "무척추동물", "육상식물", "해조식물", "고등균류"],
      species2: jdata,
    };
  },
  methods: {
    handleSearchResults(results) {
      if (results) {
        this.species = results;
      }
    },
    handleSearchResultsLength(length) {
      // Update the searchResultsLength data property with the search results length
      this.searchResultsLength = length;
    },
  },
  computed: {},
};
</script>

